module.exports = "let collector = [];\nconst records = [];\nconst res = [];\nconst Model = \"something\";\nfor (const item of res) {\n  collector.push(Model.findById(item.id));\n  if (collector.length < 10) {\n    continue;\n  }\n\n  Promise.all(collector).then(list => list.map(record => records.push(record)));\n  collector = [];\n}\n\nif (collector.length) {\n  Promise.all(collector).then(list => list.map(record => records.push(record)));\n  collector = [];\n}\n\nreturn records;\n"